.manage-inventory {
    position: relative;
    z-index: 4;
    background-color: transparent;
    color: #D6E0E7;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin-top: 70px;
    flex-direction: column;
}

.manage-inventory__alert {
    z-index: 1;
    position: absolute;
    top: 65px;
    right: 10px;
}

.manage-inventory-list, .manage-inventory-actions {
    display: block;
}

.manage-inventory-actions {
    padding-top: 10px;
    border-top: 1px solid rgba(68.808, 73.376, 86.135, 0.4);
}


.manage-inventory select {
    color: rgba(0, 0, 0, 0.87);
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
    background-color: #e0e0e0;
    padding: 6px 16px;
    font-size: 0.875rem;
    min-width: 64px;
    box-sizing: border-box;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 24px;
    height: 24px;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
}

.manage-inventory button {
    margin: 0 5px 0 0;
}

.manage-inventory-item-container {
    display: flex;
    margin-bottom: 10px;
}

.manage-inventory__quantity {
    margin-left: 30px;
}

.manage-inventory__quantity-change {
    display: flex;
    justify-content: flex-start;
}

.manage-inventory__user-list {
    margin-bottom: 10px;
}

.manage-inventory__title {
    font-weight: 700;
    margin-bottom: 5px;
}
.navbar-links {
    display: flex;
    padding-left: 28px;
    height: 100%;
    overflow-x: auto;
}

.navbar-links a:hover {
    border: none;
    text-decoration: none;
}

.navbar-link {
    display: flex;
    align-items: center;
    min-height: 56px;
    text-transform: uppercase;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.4rem;
    margin: 0 1em 0 0;
    height: 100%;
    color: #d6e0e7;
    border-bottom: 2px solid transparent;
}

.navbar-link:hover {
    transition: 0.1s ease-out;
    opacity: 0.8;
    border-bottom: 2px solid rgba(214, 224, 231, 0.2);
}

.navbar-link_active > .navbar-link {
    color: #ff005b;
    border-bottom: 2px solid #ff005b;
}

.navbar-cart {
    background: #ff005b;
    border-radius: 4px;
    margin: 1em;
    padding: 0.24em 0.5em;
    position: relative;
    text-transform: uppercase;
    font-family: Space Mono;
    font-size: 0.8em;
    color: #d6e0e7;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    text-decoration: none;
}

.navbar-cart:hover {
    text-decoration: none;
}

.navbar-logout {
    display: flex;
    flex-direction: row;
    font-size: 0.8rem;
    line-height: 2em;
    flex-direction: column;
    justify-content: stretch;
    font-family: Roboto;
    color: #ff005b;
    border: 1px solid #ff005b;
    border-radius: 4px;
    padding: 0 0.6rem;
    cursor: pointer;
}
